"use client"

import { useState } from "react"
import { Mail, Copy, Check } from "lucide-react"
import { Button } from "@/components/ui/button"
import { useToast } from "@/hooks/use-toast"

export function ProtectedEmail() {
  const [isRevealed, setIsRevealed] = useState(false)
  const [copied, setCopied] = useState(false)
  const { toast } = useToast()

  // Email is split and encoded to prevent easy scraping
  // This will be decoded on click
  const emailParts = [
    "stephens",
    "@",
    "sitesbystephens",
    ".",
    "com"
  ]

  const email = emailParts.join("")

  const handleReveal = () => {
    setIsRevealed(true)
  }

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(email)
      setCopied(true)
      toast({
        title: "Email copied!",
        description: "Paste it into your email client to contact me.",
      })
      setTimeout(() => setCopied(false), 2000)
    } catch (err) {
      toast({
        title: "Copy failed",
        description: "Please copy manually: " + email,
        variant: "destructive",
      })
    }
  }

  const handleMailto = () => {
    window.location.href = `mailto:${email}`
  }

  if (!isRevealed) {
    return (
      <Button
        onClick={handleReveal}
        variant="outline"
        className="w-full sm:w-auto text-lg py-6 px-8 border-2 hover:bg-slate-100 dark:hover:bg-slate-800"
      >
        <Mail className="w-5 h-5 mr-2" />
        Click to reveal email
      </Button>
    )
  }

  return (
    <div className="flex flex-col sm:flex-row gap-4 items-center justify-center">
      <div className="flex items-center gap-3 bg-slate-100 dark:bg-slate-800 px-6 py-4 rounded-lg border-2 border-slate-300 dark:border-slate-700">
        <Mail className="w-5 h-5 text-slate-600 dark:text-slate-400" />
        <a
          href={`mailto:${email}`}
          className="text-lg font-mono text-slate-900 dark:text-slate-100 hover:underline"
        >
          {email}
        </a>
      </div>
      <div className="flex gap-2">
        <Button
          onClick={handleCopy}
          variant="outline"
          size="lg"
          className="px-6"
        >
          {copied ? (
            <>
              <Check className="w-4 h-4 mr-2" />
              Copied!
            </>
          ) : (
            <>
              <Copy className="w-4 h-4 mr-2" />
              Copy
            </>
          )}
        </Button>
        <Button
          onClick={handleMailto}
          size="lg"
          className="px-6 bg-blue-600 hover:bg-blue-700 text-white"
        >
          Open Email
        </Button>
      </div>
    </div>
  )
}

